'use client';

import { useEffect, useState } from 'react';
import { collection, onSnapshot, query, where, orderBy } from 'firebase/firestore';
import { db } from '@/lib/firebase';
import { Currency, Media } from '@/types';
import Image from 'next/image';
import YouTube from 'react-youtube';

export default function Home() {
  const [currencies, setCurrencies] = useState<Currency[]>([]);
  const [media, setMedia] = useState<Media | null>(null);
  const [currentTime, setCurrentTime] = useState(new Date());

  useEffect(() => {
    // Listen to currency updates
    const currenciesQuery = query(
      collection(db, 'currencies'),
      orderBy('order', 'asc')
    );

    const unsubscribeCurrencies = onSnapshot(currenciesQuery, (snapshot) => {
      const currencyData = snapshot.docs.map((doc) => ({
        id: doc.id,
        ...doc.data(),
      })) as Currency[];
      setCurrencies(currencyData);
    });

    // Listen to active media
    const mediaQuery = query(
      collection(db, 'media'),
      where('isActive', '==', true)
    );

    const unsubscribeMedia = onSnapshot(mediaQuery, (snapshot) => {
      if (!snapshot.empty) {
        const mediaData = {
          id: snapshot.docs[0].id,
          ...snapshot.docs[0].data(),
        } as Media;
        setMedia(mediaData);
      }
    });

    // Update time every second
    const timer = setInterval(() => {
      setCurrentTime(new Date());
    }, 1000);

    return () => {
      unsubscribeCurrencies();
      unsubscribeMedia();
      clearInterval(timer);
    };
  }, []);

  const getYouTubeVideoId = (url: string) => {
    const match = url.match(/(?:youtu\.be\/|youtube\.com\/(?:embed\/|v\/|watch\?v=|watch\?.+&v=))([^&?]+)/);
    return match?.[1] || '';
  };

  return (
    <main className="h-screen w-screen overflow-hidden bg-white flex">
      {/* Left Side - Media Display */}
      <div className="w-1/2 h-full bg-black relative">
        {media?.type === 'youtube' ? (
          <div className="w-full h-full">
            <YouTube
              videoId={getYouTubeVideoId(media.url)}
              opts={{
                height: '100%',
                width: '100%',
                playerVars: {
                  autoplay: 1,
                  mute: 1,
                  loop: 1,
                  controls: 0,
                  modestbranding: 1,
                  rel: 0,
                },
              }}
              className="w-full h-full"
            />
          </div>
        ) : media?.type === 'image' ? (
          <Image
            src={media.url}
            alt="Advertisement"
            fill
            className="object-cover"
          />
        ) : (
          <div className="flex items-center justify-center h-full">
            <Image
              src="/logo.png"
              alt="CBZ Bank Logo"
              width={300}
              height={300}
              className="object-contain"
            />
          </div>
        )}
      </div>

      {/* Right Side - Exchange Rates */}
      <div className="w-1/2 h-full bg-gradient-to-br from-red-50 to-white p-8 flex flex-col">
        {/* Header */}
        <div className="flex justify-between items-start mb-6">
          <div>
            <Image
              src="/logo.png"
              alt="CBZ Bank"
              width={120}
              height={120}
              className="object-contain"
            />
          </div>
          <div className="text-right">
            <h1 className="text-3xl font-bold text-gray-800 mb-2">
              Today&apos;s Exchange Rates
            </h1>
            <p className="text-gray-600">
              {currentTime.toLocaleDateString('en-US', {
                weekday: 'long',
                year: 'numeric',
                month: 'long',
                day: 'numeric',
              })}
            </p>
            <p className="text-gray-600">
              {currentTime.toLocaleTimeString('en-US')}
            </p>
          </div>
        </div>

        {/* Rates Table */}
        <div className="flex-1 overflow-auto">
          <table className="w-full">
            <thead>
              <tr className="bg-cbz-red text-white">
                <th className="py-3 px-4 text-left">Currency</th>
                <th className="py-3 px-4 text-center">Buy</th>
                <th className="py-3 px-4 text-center">Sell</th>
              </tr>
            </thead>
            <tbody>
              {currencies.map((currency, index) => (
                <tr
                  key={currency.id}
                  className={`border-b border-gray-200 ${
                    index % 2 === 0 ? 'bg-white' : 'bg-red-50'
                  } hover:bg-red-100 transition-colors`}
                >
                  <td className="py-4 px-4">
                    <div className="flex items-center gap-3">
                      {currency.flagUrl && (
                        <Image
                          src={currency.flagUrl}
                          alt={currency.code}
                          width={32}
                          height={24}
                          className="rounded"
                        />
                      )}
                      <div>
                        <div className="font-semibold text-gray-800">
                          {currency.code}
                        </div>
                        <div className="text-xs text-gray-600">
                          {currency.name}
                        </div>
                      </div>
                    </div>
                  </td>
                  <td className="py-4 px-4 text-center font-mono text-lg">
                    <span className="text-cbz-red font-bold">
                      {currency.buyRate.toFixed(3)}
                    </span>
                  </td>
                  <td className="py-4 px-4 text-center font-mono text-lg">
                    <span className="text-cbz-red font-bold">
                      {currency.sellRate.toFixed(3)}
                    </span>
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>

        {/* Footer */}
        <div className="mt-4 text-center text-sm text-gray-500">
          Rates are indicative and subject to change
        </div>
      </div>
    </main>
  );
}
